import React from "react"
import { Link } from "gatsby"
import SEO from "../components/seo"
import notFound from "../images/notFound.svg"

const outerContainer = {
  width: "100vw",
  height: "100vh",
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
}

const homeBtn = {
  textDecoration: "none",
  padding: "10px 20px",
  margin: 10,
  backgroundColor: "#333",
  color: "#fff",
  borderRadius: 10,
}

const m20 = {
  margin: 20,
}

const NotFoundPage = () => (
  <div style={outerContainer}>
    <SEO title="404: Not found" />
    <img width="50%" height="50%" src={notFound} alt="404: Not Found" />
    <h2 style={m20}>Oops!!, Page Not Found</h2>
    <Link to="/" style={homeBtn}>
      Go to Home Page
    </Link>
  </div>
)

export default NotFoundPage
